@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  .wrapper {
    max-width: 520px;
  }

  .page-block {
    @extend %page-block;
  }

  .page-title {
    @extend %page-title;
    padding-bottom: 20px;
  }

  .page-description {
    @extend %page-description;
    padding-top: 0;
  }

  .page-button {
    margin-top: 10px;
  }

  .page-card {
    @extend %page-card;
    margin-top: 30px;
  }

  .mat-mdc-card {
    box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);

    & > div {
      padding: 20px;
    }
  }

  .currency-label {
    @include proxima(16px, 600, colors.$foreground_dark);
    padding: 0 15px;
    opacity: 0.6;
  }

  app-form-field {
    margin-bottom: 0 !important;

    & > *:first-child:not([form-field-label]) {
      margin-right: 20px;
    }

    &.direction--row {
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    &.requisites {
      margin-bottom: 0.5em !important;
    }
  }

  @media screen and (max-width: 600px) {
    app-form-field {
      flex-direction: column !important;

      & > *:not(:last-child) {
        margin-right: unset !important;
      }
    }
  }
}
